"use client";

import React from "react";
import {
  FaExclamationTriangle, FaRandom, FaLightbulb
} from "react-icons/fa";
import ScrollFadeUp from "./ui/ScrollFadeUp";

// --- Data ---
const PROBLEMS = [
  {
    icon: <FaExclamationTriangle size={24} />,
    text: "Primary research is either too slow, expensive, or shallow",
  },
  {
    icon: <FaRandom size={24} />,
    text: "Customer feedback is scattered and episodic",
  },
  {
    icon: <FaLightbulb size={24} />,
    text: "Teams are often forced to rely on gut-feel",
  },
];

const ProblemSection: React.FC = () => {
  return (
    <section id="problem" className="py-12 bg-transparent overflow-hidden">
      <div className="container mx-auto px-6 lg:px-8 text-center">

        {/* --- Header Section --- */}
        <ScrollFadeUp yOffset={30} duration={1} delay={0.1} className="text-3xl md:text-4xl lg:text-5xl font-black text-[#0f172a] tracking-tight leading-tight mb-12 pt-6">
          Despite more tools and data than ever before, the missing link is <span className="text-[#236a7c]">Customer Connection</span>
        </ScrollFadeUp>

        <ScrollFadeUp yOffset={30} duration={0.8} delay={0.2}>
          <div className="grid md:grid-cols-3 gap-6 mb-12 max-w-6xl mx-auto">
            {PROBLEMS.map((problem, index) => (
              <div
                key={index}
                className="group relative p-6 bg-white/60 backdrop-blur-sm rounded-3xl border border-white/60 shadow-[0_2px_20px_rgba(0,0,0,0.04)] hover:shadow-[0_20px_40px_rgba(35,106,124,0.12)] hover:-translate-y-1 transition-all duration-300 text-center hover:bg-white flex flex-col items-center justify-center h-full min-h-[320px]"
              >
                <div className="absolute top-0 inset-x-0 h-1 bg-[#236a7c] opacity-0 group-hover:opacity-100 transition-opacity duration-500 rounded-t-3xl" />

                <div className="inline-flex items-center justify-center w-16 h-16 rounded-2xl bg-[#236a7c]/5 text-[#236a7c] group-hover:bg-[#236a7c] group-hover:text-white transition-colors duration-300 text-3xl mb-5">
                  {problem.icon}
                </div>

                <p className="text-[#0f172a] text-xl font-bold leading-tight max-w-sm">
                  {problem.text}
                </p>
              </div>
            ))}
          </div>
        </ScrollFadeUp>

      </div>
    </section>
  );
};

export default ProblemSection;
